* {
    margin: 0;
}

.sidebar {
    display: flex;
    flex-direction: column;
    flex: 0.35;
}

.sidebar_search {
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}

.sidebar_searchContainer {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar_headerRight>.MuiSvgIcon-root {
    color: gray;
    padding: 10px;
}

.sidebar_searchContainer input {
    border: none;
    margin-left: 10px;
    outline: none;
}

.sidebar-header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 2px solid lightgray;
}

.sidebar_headerRight {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 5vw;
}

.sidebar_headerRight>.MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 24px !important;
}

.MuiIconButton-root {
    padding: 5px !important;
}

.sidebar-chat {
    flex: 1;
    background-color: white;
    overflow-y: scroll;
}

.Sidebarchat {
    display: flex;
    padding: 20px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
}

.Sidebarchat:hover {
    background-color: #D6D1D1;
}

.SideBarChatInfo h2 {
    font-size: 15px;
    margin-bottom: 8px;
}

.SideBarChatInfo {
    margin-left: 10px;
}

.chat {
    flex: 0.65;
    display: flex;
    flex-direction: column;
}

.chat-header {
    display: flex;
    padding: 20px;
    align-items: center;
    border-bottom: 2px solid lightgray;
}

.chat-headerInfo {
    flex: 1;
    padding-left: 20px
}

.chat-headerInfo h3 {
    margin-bottom: 3px;
    font-weight: 500;
}

.chat-headerInfo p {
    color: grey;
}

.chat-headerRight {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 100px;
}

.chat-body {
    flex: 1;
    background-image: url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png');
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow-y: scroll;
}

.default-body {
    display: grid;
    place-items: center;
}

.chat-name {
    position: absolute;
    top: -10px;
    font-weight: 800;
    font-size: xx-small;
}

.chat-message {
    position: relative;
    font-size: 16px;
    padding: 10px;
    background-color: #ffffff;
    border-radius: 10px;
    width: fit-content;
    margin-bottom: 30px;
}

.chat-receiver {
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat-time {
    position: relative;
    font-size: xx-small;
    margin-left: 10px;
}

.chat-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-bottom: 1px solid #f6f6f6;
}

.chat-footer form {
    flex: 1;
    display: flex;
}

.chat-footer form input {
    display: flex;
    flex: 1;
    border-radius: 20px;
    padding: 10px;
    border: none;
    outline: none;
}

.chat-footer form button {
    display: none;
}

.chat-footer .MuiSvgIcon-root {
    color: gray;
    padding: 10px;
}

.login {
    height: 100vh;
    width: 100 vw;
    display: grid;
    place-items: center;
}

.login-container {
    padding: 100px;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: -1px 4px 25px -4px rgba(0, 0, 0, 0.7);
}

.login-container img {
    margin-bottom: 40px;
    height: 100px;
}

.login-container button {
    margin-top: 50px;
    background-color: #0a8d48 !important;
    color: white;
}

a {
    color: black;
    text-decoration: none;
}

.menu {
    position: absolute;
    box-shadow: 0px 8px 16px 0px rgb(0 0 0 / 20%);
    overflow: auto;
    min-width: 160px;
    background-color: lightgray;
}

.menu button {
    width: 100%;
    padding: 12px 16px;
}

.menu button:hover {
    background-color: #D6D1D1
}

.menu button {
    width: 100%;
    padding: 12px 16px;
}

.helo {
    position: relative;
}

.chat-pic img {
    width: 400px;
    height: auto;
    border-radius: 25px;
}